<template>
  <div id="editPhone">
    <common-header title="修改手机"></common-header>
    <div class="input">
      <input type="text" placeholder="手机号码" v-model="mobile">
    </div>
    <div class="input">
      <input type="text" placeholder="短信验证码" v-model="code">
      <span class="btn" :class="{'sended': sended}" @click="getSMSCode">{{btnText}}</span>
    </div>
    <div class="info" v-if="sended">验证码已发送至手机： {{smsmobile}}</div>
    <div class="finish__btn" :class="{'active': isFinish}" @click="submit">完成</div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        mobile: '',
        smsmobile:'',
        code: '',
        sended: false,        //是否已发送信息
        btnText: '获取验证码',  //按钮上的文字
        checkKey: ''
      }
    },

    computed: {
      isFinish() {
        if(!this.code || !this.mobile) {
          return false;
        }else {
          return true;
        }
      }
    },

    methods: {
      getSMSCode() {
        if(this.sended) {
          return false;
        }
        
        let count = 60;
        let param = {
          a: 1,
          type: 3,
          mobile: this.mobile
        }

        this.$Api.SMSCode(param).then((res) => {
          console.log(res);
          if(res.q.s == 0) {
            this.sended = true;
            this.$toast('发送成功!');
            this.checkKey = res.q.checkKey;
						this.smsmobile = res.q.mobile;
            for(let i = count; i >= 0; i--) {
              setTimeout(() => {
                this.btnText = i + 's';
                if(i == 0) {
                  this.btnText = '获取验证码'
                  this.sended = false;
                }
              }, (count - i - 1) * 1000)
            }
          }else {
            this.$toast(res.q.d);
          }
        })
      },

      submit() {
        if(!this.isFinish) {
          return false;
        }

        // if(!this.checkKey) {
        //   this.$toast('出错了,请重新请求');
        //   return false;
        // }

        let codeParam = {
          a: 2,
          type: 3,
          mobile: this.mobile,
          code: this.code
        }

        this.$Api.SMSCode(codeParam).then((res) => {
          if(res.q.s == 0) {
            this.$Api.MobileUpdate(this.mobile, '').then((res) => {
              console.log(res);
              if(res.q.s == 0) {
                this.$toast('修改成功');
                setTimeout(()=>{
                	this.$router.replace({name:'Account'})
                },2000)
              }else {
                this.$toast(res.q.d);
              }
            })
          }else {
            this.$toast(res.q.d);
          }
        })
      }
    }
  }
</script>


<style lang="scss">
  #editPhone {
    .input {
      position: relative;
      margin: 0 0.4rem;
      line-height: 1.2rem;
      height: 1.2rem;
      border-bottom: 1px solid #eee;

      input {
        width: 100%;
        font-size: 0.4rem;
      }

      .btn {
        position: absolute;
        top: 0.2rem;
        right: 0;
        width: 2.53rem;
        height: 0.8rem;
        line-height: 0.8rem;
        text-align: center;
        color: #fff;
        background: linear-gradient(135deg, #fe9bba 0%, #de7fef 100%);
        border-radius: 0.8rem;

        &.sended {
          background: #ccc;
        }
      }
    }

    .info {
      padding: 0 0.4rem;
      padding-top: 0.3rem;
      color: #333;
    }
  }
</style>



